<template>
	<div id="current-detail">
		<public-header title="交易详情"></public-header>
		<div class="content">
			<div class="van-form-preview">
				<div class="van-form-preview__hd">
					<div class="van-form-preview__item">
						<div class="van-form-preview__label">余额变动</div>
						<div class="van-form-preview__value_in-hd" :class="{'c-primary': current.fee > 0, 'c-danger': current.fee < 0}">{{current.fee}}</div>
					</div>
				</div>
				<div class="van-form-preview__bd">
					<div class="van-form-preview__item">
						<div class="van-form-preview__label">交易类型</div>
						<div class="van-form-preview__value">{{current.trade_type_cn}}</div>
					</div>
					<div class="van-form-preview__item">
						<div class="van-form-preview__label">变动后账户余额</div>
						<div class="van-form-preview__value">￥{{current.amount}}</div>
					</div>
					<template v-if="current.trade_type == 1">
						<div class="van-form-preview__item">
							<div class="van-form-preview__label">订单号</div>
							<div class="van-form-preview__value">{{current.extra}}</div>
						</div>
					</template>
					<template v-else-if="current.trade_type == 2">
						<template v-if="getcash_log">
							<div class="van-form-preview__item">
								<div class="van-form-preview__label">订单号</div>
								<div class="van-form-preview__value">{{getcash_log.trade_no}}</div>
							</div>
							<div class="van-form-preview__item">
								<div class="van-form-preview__label">提现金额</div>
								<div class="van-form-preview__value">{{getcash_log.get_fee}}</div>
							</div>
							<div class="van-form-preview__item">
								<div class="van-form-preview__label">手续费</div>
								<div class="van-form-preview__value">{{getcash_log.take_fee}}</div>
							</div>
							<div class="van-form-preview__item">
								<div class="van-form-preview__label">实际到账</div>
								<div class="van-form-preview__value">{{getcash_log.final_fee}}</div>
							</div>
							<div class="van-form-preview__item">
								<div class="van-form-preview__label">提现人</div>
								<div class="van-form-preview__value">{{getcash_log.account.realname}}</div>
							</div>
							<div class="van-form-preview__item">
								<div class="van-form-preview__label">开始时间</div>
								<div class="van-form-preview__value">{{getcash_log.addtime_cn}}</div>
							</div>
							<div class="van-form-preview__item">
								<div class="van-form-preview__label">提现状态</div>
								<div class="van-form-preview__value">{{getcash_log.status == 1 ? '申请成功' : '申请中'}}</div>
							</div>
						</template>
					</template>
					<div class="van-form-preview__item">
						<div class="van-form-preview__label">变动时间</div>
						<div class="van-form-preview__value">{{current.addtime_cn}}</div>
					</div>
					<template v-if="current.remark">
						<div class="van-form-preview__item">
							<div class="van-form-preview__label">备注</div>
							<div class="van-form-preview__value">{{current.remark}}</div>
						</div>
					</template>
				</div>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getData} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				id: 0,
				showPreLoading: true,
				current: {},
				getcash_log: {}
			}
		},
		components: {
			publicHeader
		},
		methods: {
			onLoad(){
				var that = this;
				getData({
					vue: that,
					url: 'manage/finance/current/detail',
					data: {
						id: that.id
					},
					success: (res) => {
						this.current = that.util.extend(this.current, res.current);
						if (res.getcash_log){
							this.getcash_log = that.util.extend(this.getcash_log, res.getcash_log);
						}
					}
				})
			}
		},
		created() {
			if(this.$route.query && this.$route.query.id > 0) {
				this.id = parseInt(this.$route.query.id)
			}
		},
		mounted(){
			this.onLoad();
		}
	}
</script>

<style>

</style>


